import Vue from 'vue'

// 
import zh from '@/lang/zh/index.js';
import en from '@/lang/en/index.js';

//  vue-i18n
import VueI18n from 'vue-i18n'
Vue.use(VueI18n)

// i18n
const i18n = new VueI18n({
	// localmessagezhen
	locale: 'zh',
	// 
	messages: {
		// zhcn
		// this.$i18n.locale = zh|en|zh|xxx
		'zh': zh, // import
		'en': en,
	}
})

// H5APP
Vue.prototype._i18n = i18n

export {
	i18n
}
